.Todo{
  margin:40px 0;
  text-align:center;
  font-family:'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}
.Todo input{
  padding:10px;
  width:60%;
  display: inline;
  margin:20px;
}
.para{
  width:60%;
  margin:10px auto;
 display: flex;
 justify-content: space-between;
 padding:20px;
 border-radius:10px;
}
.para button{
  margin:0px 10px;
}